import { Component, OnInit, Output, EventEmitter, ViewChild, Inject } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { RoleService, PermissionService, CurrentUserService } from '../../../../shared';
import { jqxValidatorComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxvalidator';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxListBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxlistbox';

@Component({
  selector: 'app-role-add',
  templateUrl: './role-add.component.html',
  styleUrls: ['./role-add.component.scss']
})
export class RoleAddComponent implements OnInit {
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('myValidator') myValidator: jqxValidatorComponent;
  @ViewChild('permsListBox') permsListBox: jqxListBoxComponent;

  @Output() AddedData = new EventEmitter();
  @Output() cancelData = new EventEmitter();

  roleForm: FormGroup;
  apiUrl: string;

  permissionSource:any;
  permissionAdapter:any;

  userData: any = {};

  constructor(
    private fb: FormBuilder,
    private rols: RoleService,
    @Inject('API_URL') apiUrl: string,
    private cus: CurrentUserService
  ) {
    this.createForm();
    this.userData = this.cus.getTokenData();
    this.apiUrl = apiUrl;
  }

  ngOnInit() {
    this.createPermissionSource();
  }

  createForm() {
    this.roleForm = this.fb.group({
      'name': [null, Validators.required],
      'display_name': [null, Validators.required],
      'description': [null],
    });
  }

  createPermissionSource(){
    let url: string = this.apiUrl + 'permission?limit=all';
    // prepare the data
    this.permissionSource = {
        datatype: "json",
        datafields: [
            { name: 'id' },
            { name: 'display_name' }
        ],
        id: 'id',
        url: url,
        data:this.userData
    };
    this.permissionAdapter = new jqx.dataAdapter(this.permissionSource,{
      downloadComplete: (data, status, xhr) => {
        console.log(data);
        // this.permissionSource.totalrecords = data.data.total;
        let tableData = data.data.data;
        return tableData;
      },
      loadComplete: () => {
        // this.selectItems();
      }
    });

  }

  selectItems(){
    // console.log('Inside Select Items Function');
    // this.permsListBox.checkItem(2);
  }

  checkAll(){
    this.permsListBox.checkAll();
  }

  unCheckAll(){
    this.permsListBox.uncheckAll();
  }

  saveBtn(post) {
    let permsList = this.permsListBox.getCheckedItems();
    post['permission_id'] = [];
    for(let i =0; i < permsList.length; i++){
      post['permission_id'].push(permsList[i].value);
    }
    console.log(permsList);
      this.jqxLoader.open();
      this.rols.store(post).subscribe(
        result => {
          let data = {
            result: true,
            message: result['message']
          }
          this.AddedData.emit(data);
          this.jqxLoader.close();
        },
        error => {
          let data = {
            result:false,
            message:error['error']['error']['message']
          }
          this.AddedData.emit(data);
          this.jqxLoader.close();
        });
  }

  cancelBtn() {
    this.cancelData.emit(true);
  }
  rules =
  [
    { input: '.name', message: 'Name is required !', action: 'keyup, blur', rule: 'required' },
    { input: '.display_name', message: 'Display Name is required!', action: 'keyup, blur', rule: 'required' },
  ];

}



